.Logo {
    height: 100%;
    display: flex;
    align-items: center;
}

.Logo img {
    position: absolute;
    height: 65%;
}

.Main__nav:first-child {
    transition: opacity .19s linear .2s; 
    opacity: 0;
}

.Main__nav:last-child {
    transition: opacity .19s linear .2s; 
    opacity: 1;
}

.active:first-child{
    transition: opacity .19s linear; 
    opacity: 1;
}

.active:last-child{
    transition: opacity .19s linear; 
    opacity: 0;
}

@media screen and (max-width: 729px) {
    .Logo img {
        position: absolute;
        height: 50%;
    }
        
}